body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  text-align: center;
}

.app-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.app-intro {
  font-size: large;
}

h1 a {
  text-decoration: underline;
}


.modelTileItem {
  padding: 4px 0px;
}
.modelTileItem:hover {
  cursor: pointer;
}

.th-id {
  width: 42px;
}
.description:hover {
  cursor: auto;
}
.modelTileIntro {
  margin-bottom: 18px;
}

.model-tile {
  padding: 32px;
  margin-top: 22px;
  margin-bottom: 250px;
}
.box {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  z-index: 2000;
}

.round-items {
  font-size: 13px;
  font-weight: bold;
  color: #209cee;
}

.round-model-type {
  color: hsl(348, 100%, 61%);
}

.round-model-number {
  font-weight: bolder;
  text-decoration: underline;
}

.level-item {
  max-width: 344px;
}
